body {
  color: #000;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo__img {
  width: 40px;
  height: 40px;
  display: block;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__link {
  color: #000;
  line-height: 60px;
  text-decoration: none;
  display: block;
  position: relative;
}

.nav__link.is-active:after {
  content: "";
  background-color: #00b0f0;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.nav__link.is-active, .nav__link:hover {
  color: #00b0f0;
}

.nav__item:not(:last-child) {
  margin-right: 20px;
}
